.exit {
	width: 100vw;
	height: 100vh;
	text-align: center;
	z-index: 11;
}
.exit .blur{
	position: relative;
	text-align: center;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-filter: blur(-35px);
	        filter: blur(-35px);
	position: fixed;
	background-color: rgba(0, 0, 0, 0.473);
	z-index: 11;
	top: 0;
	left: 0;
}
.exit .blur::before {
	position: absolute;
	right: 0;
	display: block;
	width: 82%;
	height: 100vh;
	background-color: #F5F5FB;
	content: "";
	z-index: 111111;
}

.exit .blur::after {
	position: absolute;
	left: 0;
	display: block;
	width: 18%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.473);
	-webkit-filter: blur(0px);
	        filter: blur(0px);
	content: "";
	z-index: 111111;
}
.exit .blur_in{
	width: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	padding: 5px;
	position: fixed;
	top: 20%;
	left: 25%;
	z-index: 11;
}
.exit .top_name{
	width: 40%;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.exit .top_name img{
	width: 20%!important;
	margin-right: 10px;
}
.exit .top_name h6{
	margin-bottom: 0;
	font-weight: 600;
}
.exit .bottom_block{
	width: 100%;
	height: 320px;
	margin-top: 40px;
	padding: 80px 0;
	box-shadow: 0 0 5px 1px #662D91D9;
	background: #fff;
	border-radius: 5px;
	position: absolute;
	z-index: 11;
	top: 50px;
	left: 0;
}
.exit .bottom_block h4{
	color: #36005F;
	font-weight: 500;
}
.exit .bottom_block .buttons{
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	margin-top: 80px;
	padding: 0 100px;
}
.exit .bottom_block .buttons button{
	width: 100px;
	height: 56px;
	color: #fff;
	font-size: 22px;
	border: none;
	background: #662D91;
	box-sizing: border-box;
	border-radius: 8px;
	transition: 0.50s;
	outline: none;
}
.exit .bottom_block .buttons button:hover{
	background: #fff;
	border:1px solid #662D91;
	color: #000;
}
