}
.process-chart-wrap{
display: flex;
flex-direction: column;
width: 65%;
margin-left: auto;
}
.gauge {
    width: 100%;
    max-width: 30%;
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 0;
    font-family: "Roboto", sans-serif;
    font-size: 32px;
    color: #000;
    align-items: center;
}
.gauge-title {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0;
    margin: 0;
}
.gauge-title-heading {
    font-size: 24px;
    color: #aaa;
}
.gauge-body {
    width: 100%;
    height: 0;
    padding-bottom: 50%;
    background: #f2f0f0;
    position: relative;
    border-top-left-radius: 100% 200%;
    border-top-right-radius: 100% 200%;
    overflow: hidden;
}

.gauge-fill {
    position: absolute;
    top: 100%;
    left: 0;
    width: inherit;
    height: 100%;
    background: rgb(48,4,99);
    background: linear-gradient(90deg, rgba(48,4,99,1) 0%, rgba(139,15,223,1) 28%, rgba(158,20,210,1) 53%, rgba(133,34,176,1) 93%, rgba(150,0,188,1) 100%); 
    transform-origin: center top;
    transform: rotate(0.25turn);
    transition: transform 0.2s ease-out;
}

.gauge-cover {
    width: 75%;
    height: 150%;
    background: #ffffff;
    border-radius: 50%;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
    
    /* Text */
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 25%;
    box-sizing: border-box;
}

.gauge-cover::after {
    content:attr(data-text);
    position: absolute;
    top: 25px;
    color: #aaa;
    font-size: 24px;
}
.process-pie-charts {
    position: relative;
    width: 100%;
    height: 230px;
    display: flex;
    /* align-items: center; */
    justify-content: space-around;
}
.process-pie-charts .next {
    position: absolute;
    right: -10px;
    top: 34%;
    font-size: 24px;
    color: rgb(90, 4, 189);
    z-index: 10;
    cursor: pointer;
}
.process-pie-charts .prev {
    position: absolute;
    left: -10px;
    top: 34%;
    font-size: 24px;
    color: rgb(90, 4, 189);
    cursor: pointer;
    z-index: 11;
}
.gauge-pie {
    display: flex;
    justify-content: space-around;
    position: relative;
    top: 0px;
    margin-left: auto;
    width: 100%;
}


