.coninerStatus {
	margin: 40px 60px 10px;
}
.coninerStatus .track {
	position: relative;
	display: flex;
	width: 100%;
	height: 15px;
	background: #EEF2FD;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.10), inset 0px 2px 2px rgba(0, 0, 0, 0.10), inset 0px 2px 2px rgba(0, 0, 0, 0.10);
	border-radius: 21px;
}
.coninerStatus .track .start {
	font-size: 14px;
	position: absolute;
	left: 0px;
	top: -200%;
}
.coninerStatus .track .end {
	font-size: 14px;
	position: absolute;
	right: 0px;
	top: -200%;
}
.coninerStatus .track .inner {
	display: flex;
	transition: width 0.9s ease-in-out;
	position: relative;
	height: 100%;
	background: #AC03B6;
	box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 21px;
}
.coninerStatus .track .inner .current {
	font-size: 14px;
	position: absolute;
	right: -30px;
	top: -200%;
}
