.select {
    position: relative;
    display: inline-block;
    border-radius: 5px;
    width: 100%;
    margin-bottom: 5px;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    outline: none;
    margin-right: 30px;
}
.select .picker {
    width: 100%;
    height: 40px;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #EFF2FF;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 9px;
    cursor: pointer;
}
.select .picker .picker-text {
    font-size: 20px;
    margin-bottom: 0;
}

.select .options {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: -60px;
    left: 0px;
    min-width: 100%;    
    min-height: 20px;
    max-height: 150px;
    margin: 0;
    padding: 0;
    color: #fff;
    background: #EFF2FF;
    border-radius: 9px;
    overflow: auto;
    opacity: 0;
    visibility: hidden;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    z-index: 100000000;
}

.select.active .options {
    opacity: 1;
    visibility: visible;
}

.select .options li {
    width: 100%;
    padding: 2px;
    padding-left: 15px;
    padding-right: 20px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #3D1D65;
    cursor: pointer;
}
.select .options::-webkit-scrollbar {
    width: 5px;
  }

  .select .options::-webkit-scrollbar-track {
    width: 4px;
    border-radius: 20px;
    background-color: rgb(219, 110, 223);
  }
  .select .options::-webkit-scrollbar-thumb {
    /* outline: 4px solid rgb(162, 5, 162); */
    background-color: rgb(162, 5, 162);
    border-radius: 20px;
    max-height: 20px;
  }
  .select .options::-webkit-scrollbar-thumb :hover {
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: rgb(92, 15, 92);
    outline: 4px solid rgb(92, 15, 92);
  }


.select .options li:hover {
    background-color: rgb(43, 126, 221);
    background: #E8BCFD;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 9px;
}

.select .options .selected {
    position: relative;
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 30px;
    display: flex;
    align-items: center;
    text-align: center;
    background: #E8BCFD;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 9px;
    color: #3D1D65;
}

.select .options .selected:hover {
    cursor: default;
    background: #E8BCFD;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 9px;
}
