.counter{
    position:absolute;
    /* top: 20%; */
    right: 0;
}
.counter .top-caret, .counter .bottom-caret{
    width: 10px;
    height: 10px;
    position: relative;
}
.counter .top-caret:hover, .counter .bottom-caret:hover{
    cursor: pointer;
}


.counter .top-caret::after, .counter .bottom-caret::before,
.counter .bottom-caret::after, .counter .top-caret::before{
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color:  rgb(104, 100, 100); 
}
.counter .top-caret::after, .counter .bottom-caret::before{
    
    transform: rotate(50deg);
    
}
.counter .top-caret::after{    
    top: 0;
    left:6px ;
}
.counter .bottom-caret::before{    
    bottom: 0;
    left: 0;
}
.counter .bottom-caret::after, .counter .top-caret::before{
    transform: rotate(-50deg);
}
.counter .top-caret::before{    
    top: 0;
    left: 0px;
}
.counter .bottom-caret::after{     
    bottom: 0;
    left:6px;
}
